import React, { Component } from 'react'
import StarIcon from '@material-ui/icons/Star';
import Avatar from '@material-ui/core/Avatar';

export class ReviewList extends Component {
    render() {
        const { reviews } = this.props
        return (
            <div>
                <ul>
                    {reviews.map((review, idx) => {
                        return <li key={review.id}>
                            <div className="flex align-center fs12"> <Avatar className={'avatar-big'} src={review.byUser.imgUrl} />
                                <StarIcon className={'star'} fontSize="small"></StarIcon> <b>{review.rate}</b>  {review.byUser.fullname}   </div>
                            <div className="flex">{review.txt}</div> </li>
                    })}
                </ul>
            </div>
        )
    }
}