import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ButtonBase from '@material-ui/core/ButtonBase';
import Typography from '@material-ui/core/Typography';
// import { Link } from '@material-ui/core';
import { Link } from 'react-router-dom'
const images = [
    {
        url: 'https://res.cloudinary.com/ds65blvkp/image/upload/w_600/v1610905614/aliaksei-manlyx-YipPfXLCiUo-unsplash_n7ezla.jpg',
        title: 'Dance',
        width: '33%',
    }, {
        url: 'https://res.cloudinary.com/ds65blvkp/image/upload/w_600/v1610905600/chinh-le-duc-vuDXJ60mJOA-unsplash_gjku0v.jpg',
        title: 'Cooking',
        width: '67%',
    },
    {
        url: 'https://res.cloudinary.com/ds65blvkp/image/upload/w_600/v1610905862/carl-barcelo-nqUHQkuVj3c-unsplash_es2omx.jpg',
        title: 'Yoga',
        width: '55%',
    },
    {
        url: 'https://res.cloudinary.com/ds65blvkp/image/upload/w_600/v1610905865/jonathan-borba-lrQPTQs7nQQ-unsplash_yxubc7.jpg',
        title: 'Sport',
        width: '45%',
    },

    // {
    //     url: 'https://res.cloudinary.com/ds65blvkp/image/upload/v1610905603/roselyn-tirado-GDWmu0bFfS4-unsplash_retn3e.jpg',
    //     title: 'DIY',
    //     width: '50%',

    // },
    {
        url: 'https://res.cloudinary.com/ds65blvkp/image/upload/w_600/v1610905602/kari-shea-YCQFgqOzLmU-unsplash_toujku.jpg',
        title: 'Music',
        width: '45%',
    },
    {
        url: 'https://res.cloudinary.com/ds65blvkp/image/upload/w_600/v1610905590/rhondak-native-florida-folk-artist-_Yc7OtfFn-0-unsplash_sapagu.jpg',
        title: 'Art',
        width: '55%',


    }


];

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'center',
        minWidth: 300,
        minHeight: '85vh',
        maxWidth: '100%',
        width: '100%',
    },
    image: {
        // position: 'relative',
        maxHeight: '1700',
        borderBottom: '2px solid white',
        borderRight: '2px solid white',
        borderLeft: '2px solid white',
        // borderBottom: '0.5px solid white',
        [theme.breakpoints.down('xs')]: {
            width: '100% !important', // Overrides inline-style
            height: 450,
        },
        '&:hover': {
            zIndex: 1,
            '& $imageBackdrop': {
                opacity: 0.15,
            },
            '& $imageMarked': {
                opacity: 0,
            },
            '& $imageTitle': {
                // border: '4px solid currentColor',
            },
        },
    },
    focusVisible: {},
    imageButton: {
        position: 'absolute',
        left: 0,
        right: 0,
        top: 0,
        bottom: 0,
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        color: theme.palette.common.white,
    },
    imageSrc: {
        position: 'absolute',
        left: 0,
        right: 0,
        top: 0,
        bottom: 0,
        backgroundSize: 'cover',
        backgroundPosition: 'center 40%',
    },
    imageBackdrop: {
        position: 'absolute',
        left: 0,
        right: 0,
        top: 0,
        bottom: 0,
        backgroundColor: theme.palette.common.black,
        opacity: 0.4,
        transition: theme.transitions.create('opacity'),
    },
    imageTitle: {
        position: 'absolute',
        zIndex: 5,
        fontSize: 25,
        padding: `${theme.spacing(2)}px ${theme.spacing(4)}px ${theme.spacing(1) + 6}px`,
    },
    imageMarked: {
        height: 3,
        width: 18,
        // backgroundColor: theme.palette.common.white,
        position: 'absolute',
        bottom: -2,
        left: 'calc(50% - 9px)',
        transition: theme.transitions.create('opacity'),
    },
}));

export default function ButtonBases() {
    const classes = useStyles();

    return (
        <div className={classes.root}>
            {images.map((image) => (
                <ButtonBase
                    focusRipple
                    key={image.url}
                    className={classes.image}
                    // focusVisibleClassName={classes.focusVisible}
                    style={{
                        width: image.width,
                    }}
                >
                    <span
                        className={classes.imageSrc}
                        style={{
                            backgroundImage: `url(${image.url})`

                        }}
                    />
                    <span className={classes.imageBackdrop} />
                    <span className={classes.imageButton}>
                        <Typography
                            component="span"
                            variant="subtitle1"
                            color="inherit"
                            className={classes.imageTitle}
                        >
                            {image.title}
                            <span className={classes.imageMarked} />

                        </Typography>
                    </span>
                </ButtonBase>

            ))}
        </div>
    );
}
