import { Link } from 'react-router-dom'
import React, { Component } from 'react'
import { connect } from 'react-redux'
import { eventiService } from '../service/eventiService.js'
import { UtilService } from '../service/UtilService.js'
import { EventiFeaturPost, ReviewsFeaturPost } from './EventiDetailsCmps.jsx'
import FavoriteBorderRounded from '@material-ui/icons/FavoriteBorderRounded';
import Share from '@material-ui/icons/Share';
// import Box from '@material-ui/core/Box';
import { CycleList } from './../cmps/CycleList';
import Avatar from '@material-ui/core/Avatar';
import { Chat } from '../cmps/Chat.jsx'

// import Button from '@material-ui/core/Button';

import { socketService } from './../service/socketService';
// import ServerModal from '../cmps/Modal.jsx'

export class _EventiDetails extends Component {
    state = {
        eventi: null,
        isChatOpen: false,
        msg: null

    }
    componentDidMount() {
        document.getElementById('root').scroll(0, 0)
        window.scrollTo(0, 0);
        this.loadEventi()
        const { eventiId } = this.props.match.params
        socketService.setup()
        socketService.emit('looking', eventiId)
        socketService.on('message', this.joined)
        console.log('emmited');

    }


    componentDidUpdate(prevState) {
        if (prevState.eventi && this.state.eventi) {
            var didEventiChange = prevState.eventi._id !== this.state.eventi._id;
        }

        if (didEventiChange) {
            socketService.emit('looking', this.state.eventi._id)
        }
    }
    componentWillUnmount() {
        socketService.off('looking', this.state.eventi._id)
        socketService.off('message', this.joined)
        socketService.terminate()
    }

    joined = txt => {
        if (txt === null) this.setState({ msg: '' })
        else this.setState({ msg: txt })


    }
    loadEventi = async () => {
        const { eventiId } = this.props.match.params
        const eventi = await eventiService.getById(eventiId)
        this.setState({ eventi })

    }
    onToggleChat = () => {
        this.setState({ isChatOpen: !this.state.isChatOpen })
    }
    // onOpenModal = () => {

    //     this.setState({ msg: '' })
    // }

    render() {

        const { eventi, msg } = this.state;
        if (!eventi) return <h1>Loading...</h1>
        const { loggedInUser } = this.props
        if (loggedInUser && loggedInUser._id === eventi.host._id) {
            var isHost = true
        }
        else { isHost = false }
        if (msg !== null) { var message = true }
        return (<section className="eventi-details">

            <div id="myModal" className="modal">

                {message && <div className="modal-content">
                    {/* <span>&times; </span> */}
                    <p>{msg}</p>
                </div>}

            </div>

            <div className="eventi-details-grid">
                <div id="myModal" className="modal">

                    {message && <div className="modal-content flex justify-center align-center">
                        {/* <span>&times; </span> */}
                        {msg}
                    </div>}

                </div>
                < div className="share-like flex space-between" >

                    <div>   <Share className={'share'} />
                        <FavoriteBorderRounded className={'like'} /> </div>
                    <div>
                        <Link to={`/eventi`}>  <b className="capitize" > Go to All events</b> </Link>
                        {isHost && <Link to={`/eventi/edit/${eventi._id}`}>edit</Link>}
                    </div>


                </div>
                <div className="gallery">
                    <img className="pic1" alt="eventi" src={eventi.media.imgUrls[0]}></img>
                    <img className="pic2" alt="eventi" src={eventi.media.imgUrls[1]}></img>
                    <img className="pic3" alt="eventi" src={eventi.media.imgUrls[2]}></img>
                    <img className="pic4" alt="eventi" src={eventi.media.imgUrls[3]}></img>
                    <img className="pic5" alt="eventi" src={eventi.media.imgUrls[4]}></img>
                </div>
                <div className="main-details">
                    <EventiFeaturPost eventi={eventi} />
                    <hr className='divider'></hr>
                    <div className="hostFeaturedPostContent">

                        <b> Meet your host</b>

                        <div className="flex  space-between ">
                            <Link className="flex align-center" to={`/eventi/host/${eventi.host._id}`}>
                                <Avatar className={'avatar-big'} src={eventi.host.imgUrl} />
                                <h6>
                                    {eventi.host.fullname}</h6></Link> <button onClick={this.onToggleChat} >Contact Host</button>
                            {<Chat eventi={eventi} isOpen={this.state.isChatOpen} onClose={this.onToggleChat} />}
                        </div>
                        {eventi.aboutHost}

                    </div>
                    <hr className='divider'></hr>
                    <div className="review-preview">
                        <div className="flex space-between align-center">  <b>Reviews</b><div>   <Link to={`/allreviews/${eventi._id}`}>  <div className="capitize fs12">Show all Reviews</div></Link>
                            {loggedInUser !== null && <Link to={`/review/${eventi._id}`}><button>Add Review</button></Link>}
                        </div>  </div>
                        <div className="reviews">
                            <ReviewsFeaturPost reviews={eventi.reviews} />

                        </div>
                    </div>
                </div>
                <div className="cycle-list">
                    <div className="cycle-list">
                        <CycleList cycles={eventi.cycles} eventi={eventi} />

                        <Link to={`/cycles/${eventi._id}`}>  <b className="capitize" > See more dates</b> </Link>

                    </div ></div>



            </div ></section >
        )
    }
}

const mapStateToProps = state => {
    return {
        eventies: state.eventiModule.eventies,
        loggedInUser: state.userModule.loggedinUser
    }


}

const mapDispatchToProps = {
}

export const EventiDetails = connect(mapStateToProps, mapDispatchToProps)(_EventiDetails);