import React, { Component } from 'react'
import { CycleList } from './CycleList'
import { eventiService } from '../service/eventiService.js'

export class MoreDates extends Component {
    state = {
        eventi: null,

    }
    componentDidMount() {
        this.loadEventi()
    }

    loadEventi = async () => {
        const { eventiId } = this.props.match.params
        const eventi = await eventiService.getById(eventiId)
        this.setState({ eventi })

    }
    render() {
        const { eventi } = this.state
        if (!eventi) return <h1> not found</h1>
        return (
            <div className="more-dates flex justify-center">

                <div>
                    Filter Cycles
                    <form className="flex column" >
                        <input></input>
                    </form></div>
                <CycleList cycles={eventi.cycles} eventi={eventi} ></CycleList>
            </div>
        )
    }
}
