import React, { Component } from 'react'
import SearchRoundedIcon from '@material-ui/icons/SearchRounded';
export class EventiFilter extends Component {
    state = {
        filterBy: { title: '', tags: '', member: '', minPrice: 0, maxPrice: Infinity }
    };

    handleChange = async (ev) => {
        console.log(ev.target.value);
        var filterBy = { ...this.state.filterBy }
        var name = [ev.target.name]
        var value = ev.target.value
        if (ev.target.type === "number") value = +value
        filterBy[name] = value;
        console.log(filterBy);
        await this.setState({ filterBy })
        this.props.setFilter(this.state.filterBy)

        // console.log(this.state.filterBy);
    }



    render() {
        // var maxPrice = this.state.filterBy.maxPrice
        // var minPrice = this.state.filterBy.minPrice

        return (<div className="filter "><form className="flex align-center " >


            <label htmlFor="title" className="title"> <input type="text" placeholder="Search" className="title" name="title" onChange={this.handleChange}></input></label>
            <SearchRoundedIcon className="searchIcons" fontSize="large"></SearchRoundedIcon>
            <div className="flex align-center">
                {/* <label htmlFor="minPrice">Price from ${minPrice !== 0 ? minPrice : " 0"} */}
                {/* <input type="number" min="0" max="10000" name="minPrice" onChange={this.handleChange}></input></label>
                <label htmlFor="maxPrice">to Price ${maxPrice !== Infinity ? maxPrice : ''} <input value={maxPrice} type="number" min="0" max="10000" name="maxPrice" onChange={this.handleChange}></input></label>
              */}  <label htmlFor="">  <input multiple="multiple" name="tags" placeholder="Categories" onChange={this.handleChange} id="myMulti" type="list" list="presets" ></input>
                    <datalist multiple="multiple" id="presets">
                        <option >Sport</option>
                        <option>Diy</option>
                        <option>Art</option>
                        <option>Dance</option>
                        <option>Music</option>
                        <option>Cook</option>
                    </datalist></label>

            </div>

        </form></div>)
    }
}

