import React from 'react'
import { UtilService } from './../service/UtilService';


export function CycleListEdit({ cycles, onEditCycle, onRemoveCycle }) {
    return cycles.map((cycle, idx) => {
        cycle.startsAt = +cycle.startsAt
        let date = UtilService.dateFormat(cycle.startsAt)
        return <ul className="flex" key={cycle.id} >
            <div className="flex"><li>{date}</li>
                <li>{cycle.capacity}</li>
            </div>
            <div className="flex">
                <li><button onClick={() => onEditCycle(cycle.id)}>edit</button></li>
                <li><button onClick={() => onRemoveCycle(cycle.id)}>cancel</button></li>
            </div>
        </ul>
    })
}






