import React from 'react';
import { Bar } from 'react-chartjs-2';



export default class BarChart extends React.Component {
    state = {
        labels: ['Jan', 'Febr', 'Mar',
            'Apr', 'May', 'June', 'July', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
        datasets: [
            {
                label: 'Rev',
                backgroundColor: '#ee0a61',
                borderColor: 'none',
                borderWidth: 0,
                // hoverBackgroundColor: 'pink',
                // offset: true,
                // barPercentage: 0,
                barThickness: 50,
                maxBarThickness: 70,
                // minBarLength: 1,
                // order: 6,

                data: this.props.data
            }
        ]
    }

    render() {
        // console.log(this.state);
        return (
            <div>
                <Bar
                    data={this.state}
                    options={{
                        title: {
                            display: true,
                            text: 'Monthly Revenues',
                            fontSize: 20,
                            // legend: 'none'
                        },
                        scales: {
                            yAxes: [{
                                ticks: {
                                    min: 0,
                                    beginAtZero: true
                                }
                            }]
                        }
                    }}
                />
            </div>
        );
    }
}