import React, { Component } from 'react'
import { eventiService } from './../service/eventiService';
import { connect } from 'react-redux'
import { editEventi } from './../store/action/eventiActions';
import { UtilService } from '../service/UtilService';
import Button from '@material-ui/core/Button';
import SaveIcon from '@material-ui/icons/Save';



export class _ReviewEdit extends Component {
    state = {
        eventi: {},
        review: { txt: '', rate: '' }
    }
    componentDidMount() {
        this.loadEventi()
    }
    loadEventi = async () => {
        console.log(this.props.match.params);
        const { eventiId } = this.props.match.params;
        if (eventiId) {
            const eventi = await eventiService.getById(eventiId)
            this.setState({ eventi })
        }
    }

    handleInput = (ev) => {
        const { name, type } = ev.target
        var value = (type === 'checkbox') ? ev.target.checked :
            (type === 'number') ? +ev.target.value : ev.target.value
        this.setState(prevState => {
            return {
                review: {
                    ...prevState.review,
                    [name]: value
                }
            }
        })

    }
    onSaveEventi = async (ev) => {
        ev.preventDefault()
        const { eventi } = this.state
        const { loggedinUser } = this.props
        ev.preventDefault()
        let copyEventi = this.state.eventi
        if (!copyEventi.reviews || !copyEventi.reviews.length) copyEventi.reviews = []
        let copyReview = this.state.review
        copyReview.id = UtilService.makeId()
        copyReview.createdAt = Date.now()
        copyReview.byUser = loggedinUser
        copyEventi.reviews.push(copyReview)
        await this.setState(prevState => {
            return {
                eventi: {
                    ...prevState.eventi,
                    reviews: copyEventi.reviews
                },
                review: {}
            }
        })
        await this.props.editEventi(eventi)
        this.setState({ eventi: {}, review: {} })
        this.props.history.goBack()
    }

    render() {
        return (
            <React.Fragment>
                <div className="ReviewEdit"></div>
                <b className="fs30 flex justify-center" >Tell us about your expirence</b>
                <div className="edit-box">
                    <form className="form" action="" onSubmit={this.onSaveEventi}>

                        <div className="flex column align-center"
                        >                    <fieldset className="rating">
                                <input id="demo-1" type="radio" name="rate" onChange={this.handleInput} value="1"></input>
                                <label htmlFor="demo-1">1 star</label>
                                <input id="demo-2" type="radio" name="rate" onChange={this.handleInput} value="2"></input>
                                <label htmlFor="demo-2">2 stars</label>
                                <input id="demo-3" type="radio" name="rate" onChange={this.handleInput} value="3"></input>
                                <label htmlFor="demo-3">3 stars</label>
                                <input id="demo-4" type="radio" name="rate" onChange={this.handleInput} value="4"></input>
                                <label htmlFor="demo-4">4 stars</label>
                                <input id="demo-5" type="radio" name="rate" onChange={this.handleInput} value="5"></input>
                                <label htmlFor="demo-5">5 stars</label>
                                <div className="stars">
                                    <label htmlFor="demo-1" aria-label="1 star" title="1 star"></label>
                                    <label htmlFor="demo-2" aria-label="2 stars" title="2 stars"></label>
                                    <label htmlFor="demo-3" aria-label="3 stars" title="3 stars"></label>
                                    <label htmlFor="demo-4" aria-label="4 stars" title="4 stars"></label>
                                    <label htmlFor="demo-5" aria-label="5 stars" title="5 stars"></label>
                                </div>

                            </fieldset>
                            <label> Your Review
                        <textarea name="txt" type="text" required={true} onChange={this.handleInput} ></textarea>
                            </label>

                        </div>

                        <Button
                            variant="contained"
                            color="primary"
                            size="small"
                            onClick={this.onSaveEventi}
                            startIcon={<SaveIcon />}
                        >
                            Save
                          </Button>

                    </form>
                </div>

            </React.Fragment>
        )
    }
}

const mapStateToProps = state => {
    return {
        eventies: state.eventiModule.eventies,
        loggedinUser: state.userModule.loggedinUser
    };
};
const mapDispatchToProps = {
    editEventi,

}

export const ReviewEdit = connect(mapStateToProps, mapDispatchToProps)(_ReviewEdit)