import React, { Component } from 'react'
import { connect } from 'react-redux'
// import ReactDOM from 'react-dom';
import { Formik, Field, Form } from 'formik';
import { signup } from '../store/action/userActions.js'
import Uploader from '../cmps/Uploader.jsx'

class _Signup extends Component {
    state = {
        msg: '',
        signupCred: {
            username: '',
            password: '',
            fullname: '',
            imgUrl: '',

        }
    }

    doSignup = async ev => {
        ev.preventDefault()
        const { username, password, fullname, imgUrl } = this.state.signupCred
        if (!username || !password || !fullname) {
            return this.setState({ msg: 'All inputs are required' })
        }
        const signupCreds = { username, password, fullname, imgUrl }
        this.props.signup(signupCreds)
        this.setState({ signupCred: { username: '', password: '', fullname: '', imgUrl: '' } })
        this.props.history.push('/')
    }
    signupHandleChange = ev => {
        const { name, value } = ev.target
        this.setState(prevState => ({
            signupCred: {
                ...prevState.signupCred,
                [name]: value
            }
        }))

    }
    onFinishUpload = async (url) => {
        let signupCred = this.state.signupCred
        signupCred.imgUrl = url
        await this.setState({ signupCred: signupCred })
        console.log(this.state);
    }
    render() {
        return < div className="signup" >
            <h1>Sign Up</h1>
            <Formik>
                <Form onSubmit={this.doSignup}>
                    <label htmlFor="fullname"></label>
                    <Field id="fullname" name="fullname" placeholder="Fullname"
                        onChange={this.signupHandleChange} />

                    <label htmlFor="username"></label>
                    <Field id="username" name="username" placeholder="Username"
                        onChange={this.signupHandleChange} />

                    <label htmlFor="password"></label>
                    <Field
                        id="password"
                        name="password"
                        type="password"
                        onChange={this.signupHandleChange}
                        placeholder="Password"
                    /><Uploader className="uploader" onFinishUpload={this.onFinishUpload}></Uploader>
                    <button type="submit">Submit</button>

                </Form>
            </Formik>
        </div >

    }
}
const mapStateToProps = state => {
    return {
        loggedinUser: state.userModule.loggedinUser
    }
}

const mapDispatchToProps = {
    signup,

}

export const Signup = connect(mapStateToProps, mapDispatchToProps)(_Signup)