import React, { Component } from 'react'
import { connect } from 'react-redux'
import { loadEventis } from '../store/action/eventiActions.js'
import StarIcon from '@material-ui/icons/Star';
// import { EventiList } from './EventiList';
import Card from '@material-ui/core/Card';
// import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import { ReviewsFeaturPost } from './EventiDetailsCmps.jsx'
import BarChart from './BarChart';
import { Link } from 'react-router-dom'
import { UtilService } from './../service/UtilService';

export class _UserDetails extends Component {
    componentDidMount() {
        const { loggedinUser } = this.props
        if (loggedinUser) { this.props.loadEventis() }
        window.scrollTo(0, 0);
        document.getElementById('root').scroll(0, 0)
        // console.log('Got from store:', this.props)

    }

    render() {
        const { loggedinUser, eventies } = this.props
        if (!loggedinUser) return <h1>Loading</h1>
        if (!eventies) return <h1>Loading</h1>
        if (loggedinUser && eventies) { var userEventies = eventies.filter(eventi => eventi.host._id === loggedinUser._id) }
        var totalMembers = 0
        var totalRevenue = 0
        var cycleLength = 0
        var capacity = 0
        var reviewsSumRate = 0
        var totalReviewer = 0
        var reviews = []
        var monthsRevenue = [0, 10, 500, 4, 555, 400, 0, 100, 0, 0, 277, 20]
        var goingTo = 0
        eventies.forEach(eventi => {
            eventi.cycles.forEach(cycle => {
                if (cycle.members) cycle.members.forEach(member => member._id === loggedinUser._id ? goingTo++ : 0)
                else return 0
            })
        })

        userEventies.forEach(eventi => {
            cycleLength += eventi.cycles.length
            totalReviewer += eventi.reviews.length

            eventi.reviews.forEach(review => {
                reviewsSumRate += +review.rate
                reviews.push(review)
            })
            eventi.cycles.forEach(cycle => {
                if (!cycle.members) return 0
                else {
                    totalMembers += cycle.members.length
                    capacity += +cycle.capacity
                    totalRevenue += cycle.members.length * eventi.price
                    let date = new Date(cycle.startsAt)
                    date = date.getMonth()
                    monthsRevenue[date] += cycle.members.length * eventi.price
                }
            })
        });

        return (
            <div className="userDetails">

                <h1 className="flex space-between">Hello  {loggedinUser.fullname}

                    <Link to={'/eventi/edit'}>   <Button
                        variant="contained"
                        color="secondary"
                        disable="true"

                    >
                        <span className="capitize">Add Event</span>
                    </Button> </Link> </h1>
                <Card className={"root flex column"}>

                    <div className="totals capitize flex justify-center align-center space-between">
                        <ul className="capitize flex  ">
                            {/* <li className="totals capitize flex  column align-center space-between"><b> Going-To Events: </b>{goingTo}</li> */}

                            <li className="totals capitize flex  column align-center space-between"><b>Total Revenue  </b>${totalRevenue}</li>
                            <li className="totals capitize flex column align-center space-between"> <b>Future Events  </b>{cycleLength}</li>
                            {/* <li className="totals capitize flex  column align-center space-between"><b> Capacity: </b>{capacity}</li> */}
                            {/* <li className="totals capitize flex column align-center space-between"> <b>Total reviews: </b><StarIcon className={'star'} fontSize="small"></StarIcon> {reviewsSumRate / totalReviewer},({totalReviewer})</li> */}


                            <li className="totals capitize flex  column align-center space-between"><b> Hosting Events </b> {userEventies.length}</li>
                            <li className="totals capitize flex column align-center space-between"> <b>Total reviews </b><div className="flex align-center"><StarIcon className={'star'} fontSize="small"></StarIcon> {reviewsSumRate / totalReviewer} ({totalReviewer})</div></li>


                            <li className="totals capitize flex  column align-center space-between"><b> Going-To Events </b>{goingTo}</li>
                            <li className="totals capitize flex  column align-center space-between"> <b>Participants </b>{totalMembers}</li>

                            {/* <li className="totals capitize flex column align-center space-between"> <b>Cycles : </b>{cycleLength}</li>
                            <li className="totals capitize flex  column align-center space-between"><b> Capacity: </b>{capacity}</li>
                            <li className="totals capitize flex column align-center space-between"> <b>Total reviews: </b><StarIcon className={'star'} fontSize="small"></StarIcon> {reviewsSumRate / totalReviewer},({totalReviewer})</li> */}
                        </ul>

                    </div>
                </Card>
                <BarChart data={monthsRevenue}></BarChart>
                <div className="month">
                    2020 Retrospective                    <ul className="days">
                        <li><div>Jan</div>  ${monthsRevenue[0]}</li>
                        <li> <div>Feb </div> ${monthsRevenue[1]}</li>
                        <li> <div>Mar </div> ${monthsRevenue[2]}</li>
                        <li> <div>Apr</div> ${monthsRevenue[3]}</li>
                        <li> <div>May </div>${monthsRevenue[4]}</li>
                        <li> <div>June </div>${monthsRevenue[5]}</li>
                        <li> <div>July</div> ${monthsRevenue[6]}</li>
                        <li> <div>Aug </div>${monthsRevenue[7]}</li>
                        <li> <div>Sep</div> ${monthsRevenue[8]}</li>
                        <li>< div>Oct</div> ${monthsRevenue[9]}</li>
                        <li>< div>Nov </div>${monthsRevenue[10]}</li>
                        <li> <div>Dec </div>${monthsRevenue[11]}</li>

                    </ul>

                </div>

                <h1>Your Reviews</h1>
                <div className="reviews">

                    <ReviewsFeaturPost reviews={reviews} /></div>
                {/* <div className={"details"}>
                        <CardContent className={"content"}>
                            <div className="flex space-between align-center">

                            </div>

                        </CardContent>
                    </div> */}



            </div>

        )
    }
}
const mapStateToProps = state => {
    return {
        eventies: state.eventiModule.eventies,
        loggedinUser: state.userModule.loggedinUser
    };
};
const mapDispatchToProps = {

    loadEventis

}

export const UserDetails = connect(mapStateToProps, mapDispatchToProps)(_UserDetails)