import React from 'react';
import { Component } from 'react'
// import { EventiApp } from '../pages/EventiApp.jsx'
import ButtonBases from "../cmps/Buttons";
import { connect } from 'react-redux'
import { EventiList } from './EventiList.jsx'
import { loadEventis } from '../store/action/eventiActions.js'
import { Link } from 'react-router-dom'

class _Home extends Component {
    componentDidMount() {
        this.props.loadEventis(this.props.filterBy)
        // const filterBy = { member: "600825271465021c2893a7c9" }
        // this.props.loadEventis(filterBy)
        window.scrollTo(0, 0)
        console.log('Got from store:', this.props);
    }

    render() {
        const { eventies } = this.props
        let latest = eventies.slice(-10)

        // latest = latest.splice(-10, 10)
        let sports = eventies.filter(eventi => eventi.tags.includes('Sport'))
        sports = sports.slice(-5)
        let art = eventies.filter(eventi => eventi.tags.includes('Art'))
        return (
            <React.Fragment>
                <div className="full hero flex column  align-center">

                    <div className="gallery-home">

                        <div className="main-title flex column justify-center align-center logo"><h1 className="logo">Connect</h1>
                        Unique online activities. <br></br> Hosted by extraordinary people.</div>

                        <ButtonBases></ButtonBases>
                    </div>


                </div>
                <div className="flex column full justify-center">
                    <div className="sport"> <div className="flex align-center space-between title-eventi"> <div className="fs30">Most Recent  </div>  <Link to={`/eventi`} ><b className="capitize" > Go to All events</b></Link></div>
                        < EventiList eventies={latest} /></div>
                    <div className="art">     <div className="flex align-center space-between title-eventi">  <div className="fs30 ">Sport    </div><Link to={`/eventi`}><b className="capitize" > Go to All events</b></Link></div>

                        < EventiList eventies={sports} /></div>
                    <div className="sport">  <div className="flex align-center space-between title-eventi"><div className="fs30 "> Art   </div><Link to={`/eventi`}><b className="capitize" > Go to All events</b></Link></div>
                        < EventiList eventies={art} /></div>
                </div>


            </React.Fragment >
        )
    }
}
const mapStateToProps = state => {
    return {
        eventies: state.eventiModule.eventies,
        loggedinUser: state.userModule.loggedinUser,
    }
}

const mapDispatchToProps = {
    loadEventis
}
export const Home = connect(mapStateToProps, mapDispatchToProps)(_Home);
