import React, { Component } from 'react'
import { eventiService } from '../service/eventiService.js'
// import Avatar from '@material-ui/core/Avatar';
import { ReviewList } from './ReviewList';


export class AllReviews extends Component {
    state = {
        eventi: null,

    }
    componentDidMount() {
        this.loadEventi()
    }

    loadEventi = async () => {
        const { eventiId } = this.props.match.params
        const eventi = await eventiService.getById(eventiId)
        this.setState({ eventi })

    }
    render() {
        if (!this.state.eventi) return <h1>loading</h1>
        const { reviews, title } = this.state.eventi

        return (<div className="all-reviews">
            <h1>{title} - Reviews</h1>
            <ReviewList reviews={reviews} />            </div>

        )
    }
}