import React from 'react';
import { Link } from 'react-router-dom'
import Avatar from '@material-ui/core/Avatar';
import Card from '@material-ui/core/Card';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import StarIcon from '@material-ui/icons/Star';
// import IconButton from '@material-ui/core/IconButton';
import FavoriteBorderRounded from '@material-ui/icons/FavoriteBorderRounded';
import { UtilService } from '../service/UtilService';
// import Share from '@material-ui/icons/Share';
// import AttachMoneyIcon from '@material-ui/icons/AttachMoney';
// import Box from '@material-ui/core/Box';
// import { Ticket } from './Svg.jsx'


export function EventiPreview({ eventi }) {
    const { title, price, host, cycles, reviews, media } = eventi
    var total = 0
    if (!reviews || !reviews.length) {
        var eventiReviews = []

    }
    else {
        eventiReviews = reviews
        const rating = reviews.forEach(review => {
            total += parseInt(review.rate);

        })

    }

    var rating = (total / eventiReviews.length).toFixed(1)
    if (cycles[0].members) { let ticketLeft = cycles[0].capacity - cycles[0].members.length }
    let lowerTitle = title.toLowerCase()

    return (
        <div className={'EventiPreview'}>

            <Card className={'card'}>

                <Link to={`/eventi/${eventi._id}`}>
                    <CardMedia
                        className={"media"}
                        image={
                            media.imgUrls[0]
                        }
                    />
                    <CardContent className={'content'}>
                        <div className="flex space-between align-center">
                            {(total !== 0) && <div className="flex align-center" ><StarIcon className={'star'} fontSize="small"></StarIcon>
                                <span className="fs14"> {rating} <span className="b-gray">({eventiReviews.length})</span></span></div>}  <div className="flex align-center teacher"><div className="fs14 capitize">{host.fullname}</div><Avatar className={'avatar'} src={host.imgUrl} /></div>

                        </div>

                        <h1 className="capitize fs16">{lowerTitle} </h1>
                        <Typography
                            className={'MuiTypography--heading'}
                            variant={'h6'}
                            gutterBottom
                        >
                        </Typography>


                        <div className="flex space-between last-child"> <div className="flex align-center fs14"> <b className="fs18" >${price}</b>/person</div>
                            <div className="flex space-between  align-center likeshare-box" >

                                <FavoriteBorderRounded className={'like'} />

                            </div>
                        </div>

                        {/* <Divider className='divider' />
                        <div className="flex"> {cycles[0].members.map(member => (
                            <Avatar className={'avatar'} key={member._id} src={member.imgUrl} />
                        ))}</div> */}

                    </CardContent></Link>
            </Card>

        </div>
    );
}






