import React from 'react';
// import { connect } from 'react-redux'
import { UtilService } from '../service/UtilService';
import StarIcon from '@material-ui/icons/Star';
import AccessTimeIcon from '@material-ui/icons/AccessTime';
import Avatar from '@material-ui/core/Avatar';
import CastIcon from '@material-ui/icons/Cast';
import ChatBubbleOutlineIcon from '@material-ui/icons/ChatBubbleOutline';
import PeopleOutlineIcon from '@material-ui/icons/PeopleOutline';


export function EventiFeaturPost(props) {
    const { reviews } = props.eventi
    let { eventi } = props
    var total = 0
    if (!reviews || !reviews.length) {
        var eventiReviews = []

    }
    else {
        eventiReviews = reviews
        const rating = reviews.forEach(review => {
            total += parseInt(review.rate);

        })

    }

    var rating = (total / eventiReviews.length).toFixed(1)
    let lowerTitle = props.eventi.title.toLowerCase()

    return (
        <div className="eventiFeaturedPostContent">
            <div className="flex align-center space-between">
                <h1>{lowerTitle}</h1>  <div></div>
            </div>
            <div className="flex align-center space-between">
                <div className="flex align-center ">
                    {total !== 0 && <StarIcon className={'star'} fontSize="small"></StarIcon>}
                    {total !== 0 && <span> {rating}</span>}
                    {total !== 0 && <div className="b-graymain">({eventiReviews.length})</div>}
                </div>
            </div>
            <hr className='divider'></hr>
            <div className="language fs14">

                <div className="flex align-center  ">  <AccessTimeIcon fontSize="large"></AccessTimeIcon>   <div className="stuff">{props.eventi.durationMinutes} Mins</div></div>
                <div className="flex align-center ">       <CastIcon fontSize="large"></CastIcon> <div className="stuff">Join from your  {props.eventi.devices}</div></div>
                <div className="flex align-center ">     <ChatBubbleOutlineIcon fontSize="large"></ChatBubbleOutlineIcon><div className="stuff"> Hosted in {props.eventi.language}</div></div>
                <div className="flex align-center">     <PeopleOutlineIcon fontSize="large"></PeopleOutlineIcon>  <div className="stuff">{props.eventi.minPeople}</div></div>

            </div>
            <hr className='divider'></hr>

            {/* <hr className='divider'></hr> */}
            <b>What you'll do</b>
            <br></br>
            <p>  {props.eventi.description}</p>
            {/* <hr className='divider'></hr> */}
            <b> What To Bring</b>
            <br></br>
            <p>   {props.eventi.whatToBring}</p>
        </div>



    );
}

export function ReviewsFeaturPost({ reviews }) {
    if (!reviews || !reviews.length) return <div>No reviews yet</div>
    return reviews.map((review) => {
        review.createdAt = +review.createdAt
        const createdAt = UtilService.dateFormat(review.createdAt)
        return (
            <div className="reviewsFeaturedPostContent " key={review.id} >
                <div className="flex" >
                    <div className="flex align-center fs12"> <Avatar className={'avatar-big'} src={review.byUser.imgUrl} />
                        <StarIcon className={'star'} fontSize="small"></StarIcon>   <b>{review.rate}</b>  </div>
                    <div className="flex">


                    </div>
                </div>

                <div className="flex column fs12"> <div> {createdAt}</div>
                    <div> <b>{review.byUser.fullname}:</b>  {review.txt}</div>


                </div>
            </div>
        );
    })
}

