import React, { Component } from 'react'
import { connect } from 'react-redux'
import { editEventi } from '../store/action/eventiActions.js'
import { UtilService } from './../service/UtilService';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
// import Tooltip from '@material-ui/core/Tooltip';

export class _CyclesCard extends Component {
    state = {
        isMember: false
    }
    componentDidMount() {
        if (this.props.loggedInUser) {
            this.checkRegister(this.props.loggedInUser._id, this.props.cycle)
        }
        console.log(this.state);

    }
    onJoinCycle = async () => {
        const { eventi, cycle, loggedInUser } = this.props
        const eventiCopy = { ...eventi }
        console.log('eventi', eventiCopy)
        const cycleId = cycle.id
        const idx = eventiCopy.cycles.findIndex(cycle => cycle.id === cycleId)
        if (!eventiCopy.cycles[idx].members) eventiCopy.cycles[idx].members = []
        eventiCopy.cycles[idx].members.push(loggedInUser)
        console.log(eventiCopy);
        await this.props.editEventi(eventiCopy)
        this.setState({ isMember: true })

    }
    onUnjoinCycle = async () => {
        let { eventi, cycle, loggedInUser } = this.props
        let { cycles } = eventi
        let cycleId = cycle.id
        let idx = cycles.findIndex(cycle => cycle.id === cycleId)
        if (!cycles[idx].members) cycles[idx].members = []
        let members = cycles[idx].members
        members = members.filter(user => user._id !== loggedInUser._id)
        cycles[idx].members = members
        eventi = { ...eventi, ...cycles }
        await this.props.editEventi(eventi)
        this.setState({ isMember: false })
    }
    checkRegister(userId, cycle) {
        console.log(cycle, 'cycle');
        if (!userId) return
        if (!cycle) return
        let members = cycle.members
        if (!members) return
        let isMember = members.find(member => member._id === userId)
        if (isMember) {
            this.setState({ isMember: true })

        }


    }
    render() {
        const { eventi, cycle } = this.props
        const { isMember } = this.state
        cycle.startsAt = +cycle.startsAt
        cycle.capacity = +cycle.capacity

        eventi.durationMinutes = + eventi.durationMinutes
        const date = UtilService.dateFormatter(cycle.startsAt, cycle.startsAt + eventi.durationMinutes * 60 * 1000)
        if (cycle.members && cycle.capacity === cycle.members.length) {
            var fullCycle = true
        }
        else {
            fullCycle = false
        }
        const { loggedInUser } = this.props
        return (
            <Card className={"root"}>
                <div className={"details"}>
                    <CardContent className={"content"}>
                        <div>  <div className="fs14"> {date}</div>
                            {(cycle.members && cycle.members.length > 1 && !fullCycle) && <div className="fs12"> {cycle.members.length} persons already joined</div>}
                            {(cycle.members && cycle.members.length === 1 && !fullCycle) && <div className="fs12"> {cycle.members.length} person already joined</div>}</div>
                        {/* {cycle.members && <div className="fs12"> {cycle.members.length} / {cycle.capacity}</div>} */}
                        <div className="flex column ">
                            <div className="flex align-center"><b >${eventi.price}</b><div className="fs12">/person</div></div>
                            {fullCycle && <Button
                                variant="contained"
                                color="primary"
                                onClick={this.onUnjoinCycle}

                            > <span className="capitize">Sold</span></Button>}
                            {(!fullCycle && !isMember && loggedInUser !== null) && <Button
                                onClick={this.onJoinCycle}
                                variant="contained"
                                color="secondary"

                            ><span className="capitize">join</span></Button>}

                            {(!fullCycle && !isMember && !loggedInUser) && <Button
                                variant="contained"
                                color="secondary"
                                disable="true"
                                onClick={() => alert('Login first')}
                            >
                                <span className="capitize">join</span>
                            </Button>}

                            {(!fullCycle && isMember && loggedInUser !== null) && <Button
                                onClick={this.onUnjoinCycle}

                                variant="outlined"
                            >
                                <span className="capitize">joined</span>
                            </Button>}


                        </div>
                    </CardContent>


                </div>

            </Card >
        );
    }
}

const mapStateToProps = state => {
    return {
        eventies: state.eventiModule.eventies,
        loggedInUser: state.userModule.loggedinUser,
    }
}

const mapDispatchToProps = {
    editEventi
}

export const CyclesCard = connect(mapStateToProps, mapDispatchToProps)(_CyclesCard);