import React, { Component } from 'react'
import { Switch, Route } from 'react-router-dom'
import { AppHeader } from './cmps/AppHeader.jsx'
import { Home } from './pages/Home.jsx'
import { Login } from './pages/Login.jsx'
import { Signup } from './pages/Signup.jsx'
import { connect } from 'react-redux'
import { UserDetails } from './pages/UserDetails.jsx'
// import { HostDetails } from './pages/HostDetails.jsx'
// import { ChatApp } from './cmps/ChatApp.jsx'
import { EventiApp } from './pages/EventiApp.jsx'
import { EventiList } from './pages/EventiList.jsx'
import { EventiDetails } from './pages/EventiDetails.jsx'
import { EventiEdit } from './pages/EventiEdit.jsx'
import { ReviewEdit } from './pages/ReviewEdit.jsx'
import { AppFooter } from './cmps/AppFooter.jsx'
import './assets/scss/main.scss';
import { MoreDates } from './cmps/MoreDates';
import { AllReviews } from './cmps/AllReviews';
import { loadEventis } from './store/action/eventiActions.js'


class _App extends Component {
    componentDidMount() {
        this.props.loadEventis()
        // const filterBy = { member: "600825271465021c2893a7c9" }
        // this.props.loadEventis(filterBy)
        console.log('Got from store:', this.props);
    }

    render() {
        return (

            <React.Fragment>
                <AppHeader eventies={this.props.eventies} />
                < div className="main-container">
                    <Switch>
                        <Route component={EventiEdit} path="/eventi/edit/:eventiId?" />
                        <Route component={MoreDates} path="/cycles/:eventiId" />
                        <Route component={EventiDetails} path="/eventi/:eventiId" />
                        <Route component={ReviewEdit} path="/review/:eventiId" />
                        <Route component={AllReviews} path="/allreviews/:eventiId" />
                        <Route component={UserDetails} path="/user" />
                        <Route component={EventiList} path="/list" />
                        <Route component={EventiApp} path="/eventi" />
                        {/* <Route component={HostDetails} path="/eventi/host/:userId" /> */}
                        {/* <Route component={HostDetails} path="/eventi/host/:userId" /> */}


                        <Route component={Signup} path="/signup" />
                        <Route component={Login} path="/login" />
                        <Route component={Home} path="/" />
                    </Switch>

                    <AppFooter /> </ div>
            </React.Fragment>
        )
    }


}

const mapStateToProps = state => {
    return {
        eventies: state.eventiModule.eventies,
        loggedInUser: state.userModule.loggedinUser
    }
}
const mapDispatchToProps = {
    loadEventis
}


export const App = connect(mapStateToProps, mapDispatchToProps)(_App);