import React, { Component } from 'react'
import { eventiService } from './../service/eventiService';
import { connect } from 'react-redux'
import { addEventi, editEventi, removeEventi } from './../store/action/eventiActions';
import { UtilService } from '../service/UtilService';
import { CycleListEdit } from '../cmps/CycleListEdit.jsx';
import Uploader from '../cmps/Uploader.jsx'
import Button from '@material-ui/core/Button';
import SaveIcon from '@material-ui/icons/Save';
import DeleteIcon from '@material-ui/icons/Delete';

export class _EventiEdit extends Component {
    state = {
        eventi: { description: '', title: '', devices: '', language: '', minPeople: '', price: '', eventLink: '', durationMinutes: '', whatToBring: '', aboutHost: '' },
        cycle: { capacity: '', startsAt: '' }
    }

    componentDidMount() {
        this.loadEventi()
    }
    loadEventi = async () => {
        console.log(this.props.match.params);
        const { eventiId } = this.props.match.params;
        if (eventiId) {
            const eventi = await eventiService.getById(eventiId)
            this.setState({ eventi })
        }
    }
    handleInput = (ev) => {
        const { name, type } = ev.target
        var value = (type === 'checkbox') ? ev.target.checked :
            (type === 'number') ? +ev.target.value : ev.target.value
        if (name === "tags") {
            value = []
            for (var i = 0; i < ev.target.length; i++) {
                if (ev.target.options[i].selected) {
                    value.push(ev.target[i].value);
                }
            }
        }
        this.setState(prevState => {
            return {
                eventi: {
                    ...prevState.eventi,
                    [name]: value
                }
            }
        })

    }

    handleInputCycle = ({ target }) => {
        const { name, type } = target
        let value = (type === 'checkbox') ? target.checked :
            (type === 'number') ? +target.value : target.value
        if (type === 'datetime-local') {
            var d = Date.parse(value);
            d = new Date(d)
            d = Date.parse(d);
            value = d

        }
        this.setState(prevState => {
            return {
                cycle: {
                    ...prevState.cycle,
                    [name]: value
                }
            }
        })
        console.log(this.state.cycle);

    }
    onSaveCycle = async (ev) => {
        ev.preventDefault()
        let copyEventi = this.state.eventi
        if (!copyEventi.cycles || !copyEventi.cycles.length) copyEventi.cycles = []
        let copyCycle = this.state.cycle
        if (!copyCycle.id) {
            copyCycle.id = UtilService.makeId()
            copyEventi.cycles.push(copyCycle)
            this.setState(prevState => {
                return {
                    eventi: {
                        ...prevState.eventi,
                        cycles: copyEventi.cycles
                    },
                    cycle: { capacity: '', startsAt: '' }
                }
            })
        }
    }

    onSaveEventi = async (ev) => {
        ev.preventDefault()
        const { eventi } = this.state
        if (!eventi.cycles) return alert('you are rquired to add at least one cycle')

        if (!eventi._id) {
            let host = this.props.loggedinUser
            let copyEventi = eventi;
            copyEventi = { ...copyEventi, host: host, createdAt: Date.now() }
            await this.props.addEventi(copyEventi)
        }
        else await this.props.editEventi(eventi)
        this.setState({ eventi: {}, cycle: {} })
        this.props.history.push('/')
    }

    onEditCycle = (cycleId) => {
        let { cycles } = this.state.eventi
        let { eventi } = this.state
        let idx = cycles.findIndex(cycle => cycle.id === cycleId)
        this.setState({ cycle: cycles[idx] })
        eventi.cycles.splice(idx, 1)
        this.setState({ eventi })
    }
    onRemoveCycle = (cycleId) => {
        let { cycles } = this.state.eventi
        let { eventi } = this.state
        let idx = cycles.findIndex(cycle => cycle.id === cycleId)
        eventi.cycles.splice(idx, 1)
        this.setState({ eventi })
    }

    onRemoveEventi = (eventiId) => {
        this.props.removeEventi(eventiId)
        this.props.history.push('/')
    }
    onFinishUpload = async (url) => {
        let eventiCopy = this.state.eventi
        if (!eventiCopy.media) eventiCopy.media = {}
        var media = eventiCopy.media
        if (!media.imgUrls || !media.imgUrls.length) {
            var imgUrls = []
            imgUrls.push(url)
            media = { imgUrls: imgUrls }
        }
        else if (media.imgUrls) media.imgUrls.push(url)
        console.log(media);
        eventiCopy.media = media
        console.log(eventiCopy);
        await this.setState({ eventi: eventiCopy })
        console.log(this.state.eventi);
    }

    render() {
        const { cycles, description, minPeople, aboutHost, language, devices, title, price, eventLink, durationMinutes, whatToBring, _id } = this.state.eventi
        const { capacity, startsAt } = this.state.cycle
        if (!this.state.eventi) return <h1>loading.. </h1>
        if (!this.props.loggedinUser) return <h1>loading.. </h1>
        if (!this.state.cycle) return <h1>no cycle.. </h1>
        return (
            <section className="EventiEdit  align-center ">
                <div className="flex column align-center"><h1>Create Your Eventi</h1>
                    {_id && <Button onClick={() => this.onRemoveEventi(_id)}
                        variant="contained"
                        color="secondary"

                        startIcon={<DeleteIcon />}
                    >
                        Delete
      </Button>}</div>
                <div className="edit-box flex column ">

                    <form className="form" action="" onSubmit={this.onSaveEventi}>
                        <label> Title
                        <input name="title" type="text" required={true} value={title} onChange={this.handleInput}></input>
                        </label>

                        <label> Language
                        <input name="language" type="text" required={true} value={language} onChange={this.handleInput}></input>
                        </label>
                        <label> How many People can paricipate <br></br> <span className="fs12">(ex: minimum of 3 people. for  Private groups send a request on chat)</span>
                            <input name="minPeople" type="text" required={true} value={minPeople} onChange={this.handleInput}></input>
                        </label>
                        <label> Devices
                        <input name="devices" type="text" required={true} value={devices} onChange={this.handleInput}></input>
                        </label>
                        <label> Price
                        <input name="price" type="number" min="0" required={true} value={price} onChange={this.handleInput}></input>
                        </label>          <label> Duration (Minutes)
                            <input name="durationMinutes" type="number" required={true} mvalue={durationMinutes} onChange={this.handleInput} ></input>
                        </label>



                        <label htmlFor="">Categories</label>
                        <select multiple="multiple" name="tags" required={true} onChange={this.handleInput} id="myMulti">
                            <option>Sport</option>
                            <option>Art</option>
                            <option>Dance</option>
                            <option>Music</option>
                            <option>Cook</option>
                        </select>

                        <label> Event Link
                        <input name="eventLink" type="text" required={true} value={eventLink} onChange={this.handleInput} ></input>
                        </label>



                        <label> About Host<textarea name="aboutHost" type="text" required={true} value={aboutHost} onChange={this.handleInput}></textarea></label>
                        <label> Description
                        <textarea name="description" type="text" required={true} value={description} onChange={this.handleInput} ></textarea>
                        </label>
                        <label >  What To Bring<textarea name="whatToBring" type="text" required={true} value={whatToBring} onChange={this.handleInput}></textarea></label>


                        <div className="uploader-container flex">
                            <Uploader onFinishUpload={this.onFinishUpload}></Uploader>
                        </div>







                        <Button
                            variant="contained"
                            color="primary"
                            size="small"
                            onClick={this.onSaveEventi}
                            startIcon={<SaveIcon />}
                        >
                            Save
                          </Button>
                    </form>

                    <div className="cyclesBox"> <h2>Cycles</h2>
                        <form className="flex align-center" onSubmit={this.onSaveCycle}>
                            <input name="capacity" required={true} type="number" value={capacity} onChange={this.handleInputCycle} placeholder="capacity"></input>
                            <input name="startsAt" required={true} type="datetime-local" value={startsAt} onChange={this.handleInputCycle} placeholder="title"></input>
                            <Button
                                onClick={this.onSaveCycle}
                                variant="contained"
                                color="primary"
                                size="small"
                                startIcon={<SaveIcon />}
                            >
                                add/update
      </Button>
                        </form>
                        {cycles && <CycleListEdit cycles={cycles} onRemoveCycle={this.onRemoveCycle} onEditCycle={this.onEditCycle} />}
                    </div>

                </div>


            </section >

        )
    }

}
const mapStateToProps = state => {
    return {
        eventies: state.eventiModule.eventies,
        loggedinUser: state.userModule.loggedinUser,
    };
};
const mapDispatchToProps = {
    editEventi,
    addEventi,
    removeEventi
}

export const EventiEdit = connect(mapStateToProps, mapDispatchToProps)(_EventiEdit)