import React, { Component } from 'react'
import { connect } from 'react-redux'
import { socketService } from '../service/socketService.js'
import Avatar from '@material-ui/core/Avatar';
import SendIcon from '@material-ui/icons/Send';
import CloseIcon from '@material-ui/icons/Close';

class _Chat extends Component {
    state = {
        msg: { txt: '' },
        msgs: [{ from: this.props.eventi.host.imgUrl, txt: 'Hi there thanks for reaching out!' }],
        isBotMode: false,
        currTypingUser: {
            username: '',
            isTyping: false
        }
    }

    componentDidMount() {
        // const savedMsgs = socketService.getMsgsFromStorage() || [];
        // this.setState({ msgs: savedMsgs });

        socketService.setup()
        socketService.emit('chat topic', this.props.eventi._id)

        socketService.on('chat addMsg', this.addMsg)

        socketService.on('userTyping', this.setUserTyping)
    }

    componentDidUpdate(prevProps) {
        const didEventiChange = prevProps.eventi._id !== this.props.eventi._id;

        if (didEventiChange) {
            socketService.emit('chat topic', this.props.eventi._id)
        }
    }

    componentWillUnmount() {
        socketService.off('chat addMsg', this.addMsg)
        socketService.off('userTyping', this.setUserTyping)

        socketService.terminate()
        clearTimeout(this.timeout)
    }

    setUserTyping = ({ username, msg }) => {

        this.setState({ currTypingUser: { username, isTyping: !!msg } }, () => {
            console.log('currTypingUser: ', this.state.currTypingUser);
        })
    }


    addMsg = newMsg => {

        this.setState(prevState => ({ msgs: [...prevState.msgs, newMsg] }), () => {

            // socketService.saveMsgsToStorage(this.state.msgs)
        })
        if (this.state.isBotMode) this.onSendBotResponse();
    }

    onSendBotResponse = () => {
        // Handle case: send single bot response (debounce).
        this.timeout && clearTimeout(this.timeout)

        this.timeout = setTimeout(this.sendBotResponse, 1500)
    }

    sendBotResponse = () => {
        this.setState(prevState => ({ msgs: [...prevState.msgs, { from: 'Bot', txt: 'You are amazing!' }] }), () => {
            // socketService.saveMsgsToStorage(this.state.msgs)
        })
    }

    sendMsg = ev => {
        ev.preventDefault()

        const from = this.props.loggedinUser?.imgUrl || this.props.loggedinUser?.username || 'Me'

        socketService.emit('chat newMsg', { from, txt: this.state.msg.txt })
        this.setState({ msg: { from: 'Me', txt: '' } })
    }

    msgHandleChange = async ev => {
        const { name, value } = ev.target
        const user = {
            username: this.props.loggedinUser?.fullname || this.props.loggedinUser?.username || 'guest',
            msg: value
        }
        socketService.emit('typing', user)

        this.setState(prevState => {
            return {
                msg: {
                    ...prevState.msg,
                    [name]: value
                }
            }
        })
    }

    render() {
        const { currTypingUser, msg } = this.state
        const { isOpen, eventi, loggedinUser } = this.props

        return (
            <div className={`chat ${isOpen && 'active'}`}>
                <b className="chat-topic chat-layout flex space-between">  <div className="flex  space-between" ><div className="flex align-center" >
                    <Avatar className={'avatar'} src={eventi.host.imgUrl} />
                    <div>{eventi.host.fullname}</div></div>
                </div><b onClick={this.props.onClose}><CloseIcon></CloseIcon></b></b>
                {/* <label>
                    <input
                        type="checkbox"
                        name="isBotMode"
                        checked={this.state.isBotMode}
                        onChange={(ev) => this.setState({ isBotMode: ev.target.checked })}
                        className="chat-layout"
                    />
          Bot Mode
        </label> */}
                <h3>{currTypingUser?.isTyping && currTypingUser.username + ' is typing...'}</h3>

                <div className="chat-main-content">
                    <ul className="chat-msgs chat-layout">
                        {this.state.msgs.map((msg, idx) => (
                            <div key={idx} className="flex align-center"><span className="from ">
                                <Avatar className={'avatar'} src={msg.from} /></span>{msg.txt}</div>
                        ))}
                    </ul>  <hr ></hr>
                    <form className="msg-form flex align-center space-between" onSubmit={this.sendMsg}>

                        <input
                            type="text"
                            value={msg.txt}

                            onChange={this.msgHandleChange}
                            name="txt"
                            className="msg-input"
                            autoComplete="off" />
                        <SendIcon onClick={this.sendMsg} color="secondary"></SendIcon>
                    </form>
                </div >
            </div >
        )
    }
}

const mapStateToProps = state => {
    return {
        loggedinUser: state.userModule.loggedinUser,
    }
}
const mapDispatchToProps = {
}

export const Chat = connect(mapStateToProps, mapDispatchToProps)(_Chat)
