import { EventiPreview } from "../cmps/EventiPreview.jsx"
// import ButtonBases from "../cmps/Buttons";
// import { Button } from "@material-ui/core";
export function EventiList({ eventies }) {

    return <section className="eventiList-container">
        <div className="EventiList">
            {eventies.map(eventi => {
                return <EventiPreview key={eventi._id} eventi={eventi} />
            })}
        </div>
    </section>
}